

























































module.exports =
  mixins: [
    require("~plugins/get-by-mount") "24h", "sow/story", -> @book_id
    require '~plugins/book'
    require('~plugins/pager')
      watch: (val, key)->
        { chat_id } = @
        if chat_id
          @$nextTick ->
            @$store.commit "menu/focus", chat_id
  ]
  methods:
    focus: (@idx)->
    anker: (book_id, a)->
      @$router.push
        path: "../#{@part_id}/anker"
        query: { a, @back }

  computed:
    page_all_contents: ->
      @chats(@part_id)




